*{
  padding:0;
  margin:0;
}

body{
  padding:60px;
}
button{
margin:5px 20px 5px 20px; 

}

.container-fluid {
max-width: 650px;
margin: 40px auto 100px;
}



body {
font-family: Verdana;
}
li {
list-style: none;
}

.queList {
margin-bottom: 40px;
}



.q-main-title {
margin-bottom: 40px;
}
.q-main-title-h1 {
font-size: 40px;
font-weight: 600;
margin-bottom: 16px;
}
.q-main-title-p {
font-size: 16px;
}

.q-top {
display: -ms-flexbox;
display: flex;
-ms-flex-align: start;
    align-items: flex-start;
margin-bottom: 8px;
}

.q-title {
font-size: 24px;
-ms-flex: 1 1;
    flex: 1 1;
line-height: 1.3;
}
.q-img {
margin-bottom: 16px;
}
.q-img img {
width: 100%;
}
.q-number {
font-size: 12px;
margin-bottom: 16px;
letter-spacing: 2px;
line-height: 24px;
}
.q-container {
display: -ms-flexbox;
display: flex;
-ms-flex-direction: column;
    flex-direction: column;
margin-bottom: 40px;
}
.q-final {
display: -ms-flexbox;
display: flex;
-ms-flex-pack: justify;
    justify-content: space-between;
}



.q-question {
margin-bottom: 56px;
}
.q-list {
display: -ms-flexbox;
display: flex;
-ms-flex-direction: column;
    flex-direction: column;
margin-bottom: 16px;
padding: 0!important;
}
.q-list-item {
display: -ms-flexbox;
display: flex;
-ms-flex-align: center;
    align-items: center;
margin: 0!important;
}
.q-button {
border: 0;
line-height: 1.3;
border-bottom: 1px solid #bbb;
background: #fff;
padding: 8px 40px 6px 0;
color: #000;
font-size: 18px;
width: 100%;
text-align: left;
-webkit-transition: 0.2s;
-o-transition: 0.2s;
transition: 0.2s;
}
.q-button:hover {
color: var(--green);
border-bottom-color: var(--green);
cursor: pointer;
}



:root {
--green: #4eb454;
--green: #00bf0b;
--red: #b32d2d;
--blue: #2450ff;
}

.q-false {
background: #666;
}

.q-select, .q-select:hover {
color: #bbb;
border-bottom-color: #bbb;
}
.q-true div, q-true div:hover{
color: var(--green)!important;
border-bottom-color: var(--green)!important;
font-weight: 600;
}
.q-user-select {
color: #000;
}

.q-user-select:before {
content: "âœ“";
font-size: 120%;
position: absolute;
margin-left: -16px;
}

.q-true.q-user-select:before {
color: var(--green);
}

